@import 'variable';

@color: rgb(122, 191, 223);
@hover: rgb(72, 143, 209);
.gt-container {
	font-family: @font-content !important;
	font-size: 1em !important;
	margin-top: 3em;
	.gt-comment-content,
	.gt-header-textarea {
		border-radius: 4px !important;
		background: #fafafa !important;
		box-shadow: 0 3px 6px rgba(102, 102, 102, 0.16), 0 3px 6px rgba(110, 110, 110, 0.23) !important;
		transition: 0.3s !important;
		&:hover {
			box-shadow: 0 5px 8px rgba(102, 102, 102, 0.3), 0 5px 8px rgba(110, 110, 110, 0.3) !important;
		}
	}
	.gt-avatar img {
		border-radius: 4px !important;
		box-shadow: 0 3px 6px hsla(0, 0%, 40%, 0.16), 0 3px 6px hsla(0, 0%, 43%, 0.23) !important;
		transition: all 0.25s ease 0s, transform 0.5s cubic-bezier(0.6, 0.2, 0.1, 1) 0s,
			opacity 0.5s cubic-bezier(0.6, 0.2, 0.1, 1) 0s !important;
		&:hover {
			box-shadow: 0 10px 20px hsla(0, 0%, 41%, 0.19), 0 6px 6px hsla(0, 0%, 39%, 0.23) !important;
			transform: translateY(-4px);
		}
	}
	.gt-comment-username {
		&:hover {
			text-decoration: none !important;
		}
	}
	.gt-copyright {
		display: none !important;
	}
	.gt-header-controls-tip {
		color: @color !important;
	}
	.gt-ico-heart .gt-svg svg {
		fill: @color !important;
		transition: all 0.2s !important;
		&:hover {
			fill: red !important;
		}
	}
	.gt-comment-like:hover {
		border-bottom-color: transparent !important;
	}

	.gt-ico-github svg {
		fill: #666 !important;
	}
	.gt-comment-username {
		color: @color !important;
	}
	.gt-comment-text,
	.gt-comment-date {
		text-shadow: #d9d9d9 0 0 1px, #fffffb 0 0 1px, #fffffb 0 0 2px !important;
	}
	.gt-comment-content {
		transition: all 0.25s ease 0s, transform 0.5s cubic-bezier(0.6, 0.2, 0.1, 1) 0s,
			opacity 0.5s cubic-bezier(0.6, 0.2, 0.1, 1) 0s !important;
		&:hover {
			box-shadow: 0 10px 20px rgba(105, 105, 105, 0.19), 0 6px 6px rgba(100, 100, 100, 0.23) !important;
			transform: translateY(-4px) !important;
		}
	}
	.gt-comment-edit,
	.gt-comment-like,
	.gt-comment-reply {
		border: none !important;
		position: absolute !important;
		&:hover:after {
			left: 0 !important;
			width: 0% !important;
			outline: 0 !important;
			text-decoration: none !important;
		}
	}
	a.gt-avatar-github {
		text-decoration: none !important;
		border-bottom: none !important;
		&:after {
			text-decoration: none !important;
			border-bottom: none !important;
		}
	}
}

.gt-container .gt-comments-null {
	text-align: center !important;
	font-size: 1.1em !important;
	line-height: 1.1em !important;
	outline: none !important;
	color: #808080 !important;
	user-select: none !important;
}

.aws-btn {
	--button-default-height: 48px;
	--button-default-font-size: 14px;
	--button-default-border-radius: 6px;
	--button-horizontal-padding: 20px;
	--button-raise-level: 2px;
	--button-hover-pressure: 2;
	--transform-speed: 0.185s;
	--button-primary-color: #fafafa;
	--button-primary-color-dark: #67cbc3;
	--button-primary-color-light: #349890;
	--button-primary-color-hover: #ecf9f8;
	--button-primary-color-active: #b9cecc;
	--button-primary-border: 2px solid #b3e5e1;
}
